  --blue: #A1C3d1;
  --purple: #b39bc8;
  --white:#F0EBF4;
  --salmon: #F172A1;
  --pink: #E64398;
  --bright-white: #ffffff;
}

body,main{
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5{
  margin: 0;
  padding: 0;
}


body{
  
  background-color: #ffffff;
  
  background-color: var(--bright-white)
}




:root {
	--blue: #a1c3d1;
	--purple: #b39bc8;
	--white: #f0ebf4;
	--salmon: #f172a1;
	--pink: #e64398;
	--black: #444444;
	--bright-white: #f3f3f3;
	--node-diameter: 24px;
}

* {
	box-sizing: border-box;
}

.graph-unit {
	height: 24px;
	height: var(--node-diameter);
	width: 24px;
	width: var(--node-diameter);
	display: flex;
	align-items: center;
	justify-content: center;
}

.normal-node {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-color: #b39bc8;
	background-color: var(--purple);
	border: 1px gray solid;
}

.start-node {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-color: green;
}

.finish-node {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-color: red;
}

.left-right-edge {
	background-color: gray;
	height: 3px;
	width: 100%;
}

.up-down-edge {
	height: 100%;

	background-color: gray;
	width: 3px;
}

.visited-node {
	width: 100%;
	height: 100%;
	border-radius: 50%;

	-webkit-animation: visited-node-animation 3s forwards;

	        animation: visited-node-animation 3s forwards;
}

@-webkit-keyframes visited-node-animation {
	0% {
		background-color: #f172a1;
		background-color: var(--salmon);
		height: 1px;
		width: 1px;
	}

	50% {
		background-color: purple;
	}

	100% {
		background-color: #e64398;
		background-color: var(--pink);
		height: 24px;
		height: var(--node-diameter);
		width: 24px;
		width: var(--node-diameter);
	}
}

@keyframes visited-node-animation {
	0% {
		background-color: #f172a1;
		background-color: var(--salmon);
		height: 1px;
		width: 1px;
	}

	50% {
		background-color: purple;
	}

	100% {
		background-color: #e64398;
		background-color: var(--pink);
		height: 24px;
		height: var(--node-diameter);
		width: 24px;
		width: var(--node-diameter);
	}
}

.node-in-queue {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-color: blue;
}

@-webkit-keyframes shortest-path-animation {
	0% {
		background-color: #a1c3d1;
		background-color: var(--blue);
		height: 1px;
		width: 1px;
	}
	50% {
		background-color: #f172a1;
		background-color: var(--salmon);
	}
	100% {
		background-color: purple;
		height: 24px;
		height: var(--node-diameter);
		width: 24px;
		width: var(--node-diameter);
	}
}

@keyframes shortest-path-animation {
	0% {
		background-color: #a1c3d1;
		background-color: var(--blue);
		height: 1px;
		width: 1px;
	}
	50% {
		background-color: #f172a1;
		background-color: var(--salmon);
	}
	100% {
		background-color: purple;
		height: 24px;
		height: var(--node-diameter);
		width: 24px;
		width: var(--node-diameter);
	}
}

.shortest-path-node {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-color: purple;
	-webkit-animation: 1s forwards shortest-path-animation;
	        animation: 1s forwards shortest-path-animation;
}

.graph_container {
}

.graph-col {
	display: flex;
}

.node-selection-menu {
	position: absolute;

	background-color: white;
}

.node-selection-menu-option {
	text-align: center;
	padding: 0.5rem;
	border: 1px lightgray solid;
}

.node-selection-menu-option:hover {
	cursor: pointer;
	background-color: whitesmoke;
}

.tool-bar {
  display: flex;
  align-items: center;
  padding: 2rem;
  grid-gap: 10px;
  gap: 10px;
}

